<template>
  <div>
    <el-card class="">
      <div slot="header" class="clearfix">
        <span>菜单管理</span>
      </div>
      <el-alert title="Tips" type="warning" description="设置后台管理系统的左侧菜单，目前至多支持两层菜单，三层以上菜单将不会显示。" show-icon
        :closable="false">
      </el-alert>
      <!-- 树形table -->
      <el-table :data="allMenus" row-key="permission_id" :expand-row-keys="['0']" border stripe max-height="600"
        class="am-margin-top">
        <el-table-column label="资源名称" width="200" fixed="left">
          <template slot-scope="scope">
            <template v-if="scope.row.resource_type_id === 2">
              <span>{{ scope.row.menu_name }}</span>
            </template>
            <template v-if="scope.row.resource_type_id === 3">
              <span>{{ scope.row.permission_remark }}</span>
            </template>
          </template>
        </el-table-column>
        <el-table-column label="图标">
          <template slot-scope="scope">
            <i :class="`el-icon-${scope.row.icon_name}`"></i>
          </template>
        </el-table-column>
        <el-table-column label="资源类型">
          <template slot-scope="scope">
            <template v-if="scope.row.resource_type_id === 2">
              <el-tag>{{ scope.row.resource_type_name }}</el-tag>
            </template>
            <template v-if="scope.row.resource_type_id === 3">
              <el-tag type="warning">{{ scope.row.resource_type_name }}</el-tag>
            </template>
          </template>
        </el-table-column>
        <el-table-column prop="resource_type_code" label="资源代码" width="100">
        </el-table-column>
        <el-table-column prop="permission_code" label="权限代码" width="180">
        </el-table-column>
        <el-table-column prop="operation_name" label="操作名称">
        </el-table-column>
        <el-table-column prop="route_name" label="路由名称" width="110">
        </el-table-column>
        <el-table-column prop="route_full_path" label="完整路由地址" width="150">
        </el-table-column>
        <el-table-column prop="menu_order" label="菜单序号">
        </el-table-column>
        <el-table-column prop="permission_remark" label="备注" width="180">
        </el-table-column>
        <el-table-column label="操作" width="230" fixed="right">
          <template slot-scope="scope">
            <el-button @click="openEditDialog(scope.row)" :disabled="scope.row.permission_id === 0" icon="el-icon-edit"
              type="primary" title="编辑" plain>
            </el-button>
            <el-button @click="openInsertDialog(scope.row)" v-if="scope.row.resource_type_id !== 3"
              icon="el-icon-circle-plus-outline" type="success" title="添加" plain>
            </el-button>
            <el-button @click="openIconDialog(scope.row)" :disabled="scope.row.permission_id === 0"
              v-if="scope.row.resource_type_id !== 3" icon="el-icon-setting" type="warning" title="设置图标" plain>
            </el-button>
            <el-button @click="handleRemove(scope.row)" :disabled="scope.row.permission_id === 0" icon="el-icon-delete"
              type="danger" title="删除" plain>
            </el-button>
          </template>
        </el-table-column>
      </el-table>

      <!-- 添加Dialog -->
      <el-dialog title="添加菜单/操作按钮" :visible.sync="insert_dialog_visible" @closed="handleClosedDialog('insert')">
        <el-tabs v-model="insert_tab_selected" type="card">
          <el-tab-pane label="菜单" name="insert_menu_form">
            <el-form ref="insert_menu_form" :model="insert_menu_form" :rules="rules" label-width="120px">
              <el-form-item label="菜单名称" prop="menu_name">
                <el-input v-model="insert_menu_form.menu_name"></el-input>
              </el-form-item>
              <el-form-item label="关联路由" prop="route_id">
                <el-cascader v-model="insert_menu_form.route_id" :options="routes_tree"
                  @change="handleRouteChange($event, 'insert_menu_form')"
                  :props="{ value: 'route_id', label: 'route_name', emitPath: false, checkStrictly: true }"
                  placeholder="指定此菜单对应的路由">
                </el-cascader>
              </el-form-item>
              <el-form-item label="显示顺序" prop="menu_order">
                <el-input v-model="insert_menu_form.menu_order" placeholder="显示顺序按照数字从小到大，如2001"></el-input>
              </el-form-item>
              <el-form-item label="权限代码" prop="permission_code">
                <el-input v-model="insert_menu_form.permission_code" placeholder="权限代码由父级权限和菜单权限合成">
                </el-input>
              </el-form-item>
              <el-form-item label="权限备注" prop="permission_remark">
                <el-input v-model="insert_menu_form.permission_remark" placeholder="菜单备注信息"></el-input>
              </el-form-item>
            </el-form>
          </el-tab-pane>
          <el-tab-pane label="操作按钮" name="insert_operation_form">
            <el-form ref="insert_operation_form" :model="insert_operation_form" :rules="rules" label-width="120px">
              <el-form-item label="操作类型" prop="operation_id">
                <el-select v-model="insert_operation_form.operation_id" placeholder="请选择操作按钮类型">
                  <el-option v-for="item in operations" :key="item.operation_id" :label="item.operation_name"
                    :value="item.operation_id">
                  </el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="权限代码" prop="permission_code">
                <el-input v-model="insert_operation_form.permission_code" placeholder="权限代码由父级权限和按钮权限合成"></el-input>
              </el-form-item>
              <el-form-item label="权限备注" prop="permission_remark">
                <el-input v-model="insert_operation_form.permission_remark" placeholder="请输入权限备注信息"></el-input>
              </el-form-item>
            </el-form>
          </el-tab-pane>
        </el-tabs>
        <div slot="footer" class="dialog-footer">
          <el-button @click="insert_dialog_visible = false">取 消</el-button>
          <el-button type="primary" @click="handleInsert">添 加</el-button>
        </div>
      </el-dialog>
      <!-- 编辑Dialog -->
      <el-dialog title="编辑节点" :visible.sync="edit_dialog_visible" @closed="handleClosedDialog('edit')">
        <el-tabs v-model="edit_tab_selected" type="card">
          <el-tab-pane :disabled="edit_tab_selected === 'edit_operation_form'" label="菜单" name="edit_menu_form">
            <el-form ref="edit_menu_form" :model="edit_menu_form" :rules="rules" label-width="120px">
              <el-form-item label="菜单名称" prop="menu_name">
                <el-input v-model="edit_menu_form.menu_name"></el-input>
              </el-form-item>
              <el-form-item label="关联路由" prop="route_id">
                <el-cascader v-model="edit_menu_form.route_id" :options="routes_tree"
                  @change="handleRouteChange($event, 'edit_menu_form')"
                  :props="{ value: 'route_id', label: 'route_name', emitPath: false, checkStrictly: true }"
                  placeholder="指定此菜单对应的路由">
                </el-cascader>
              </el-form-item>
              <el-form-item label="显示顺序" prop="menu_order">
                <el-input v-model="edit_menu_form.menu_order" placeholder="显示顺序按照数字从小到大，如2001"></el-input>
              </el-form-item>
              <el-form-item label="权限代码" prop="permission_code">
                <el-input v-model="edit_menu_form.permission_code" placeholder="权限代码由父级权限和菜单权限合成">
                </el-input>
              </el-form-item>
              <el-form-item label="权限备注" prop="permission_remark">
                <el-input v-model="edit_menu_form.permission_remark" placeholder="菜单备注信息"></el-input>
              </el-form-item>
            </el-form>
          </el-tab-pane>
          <el-tab-pane :disabled="edit_tab_selected === 'edit_menu_form'" label="操作按钮" name="edit_operation_form">
            <el-form ref="edit_operation_form" :model="edit_operation_form" :rules="rules" label-width="120px">
              <el-form-item label="操作类型" prop="operation_id">
                <el-select v-model="edit_operation_form.operation_id" placeholder="请选择操作按钮类型">
                  <el-option v-for="item in operations" :key="item.operation_id" :label="item.operation_name"
                    :value="item.operation_id">
                  </el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="权限代码" prop="permission_code">
                <el-input v-model="edit_operation_form.permission_code" placeholder="权限代码由父级权限和按钮权限合成"></el-input>
              </el-form-item>
              <el-form-item label="权限备注" prop="permission_remark">
                <el-input v-model="edit_operation_form.permission_remark" placeholder="请输入权限备注信息"></el-input>
              </el-form-item>
            </el-form>
          </el-tab-pane>
        </el-tabs>
        <div slot="footer" class="dialog-footer">
          <el-button @click="edit_dialog_visible = false">取 消</el-button>
          <el-button type="primary" @click="handleEdit">保 存</el-button>
        </div>
      </el-dialog>
      <!-- 选择图标Dialog -->
      <el-dialog title="选择图标" width="72%" :visible.sync="icon_modal_visible" @closed="handleClosedIconDialog">
        <!-- 图标列表 -->
        <ul class="icon-list">
          <li v-for="icon in icons" @click="handleCheckIcon(icon)" :key="icon.icon_id" :class="{ checked: icon.checked }">
            <span>
              <i :class="`el-icon-${icon.icon_name}`"></i>
              <span class="icon-name">el-icon-{{ icon.icon_name }}</span>
            </span>
          </li>
        </ul>
        <!-- 分页器 -->
        <el-pagination background layout="->,prev, pager, next" :page-size="pageSize" :total="total_icon"
          @current-change="handlePageChange">
        </el-pagination>

        <div slot="footer" class="dialog-footer">
          <el-button @click="icon_modal_visible = false">取 消</el-button>
          <el-button @click="handleUpdateIcon" type="primary">确 定</el-button>
        </div>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
import { mapActions, mapGetters, mapState } from 'vuex';
import { Icon } from "@/api/";

export default {
  name: "Menu",
  data() {
    return {
      // 当前操作的菜单
      current_node: '',
      // 添加--选项卡状态
      insert_tab_selected: 'insert_menu_form',
      // 添加Dialog
      insert_dialog_visible: false,
      insert_menu_form: {
        menu_name: '',
        menu_order: '',
        route_id: null,
        route_name: '',
        route_full_path: '',
        parent_id: '',
        permission_code: '',
        permission_remark: '',
        resource_type_id: 2,
        resource_type_code: "MENU",
        resource_type_name: "菜单",
      },
      insert_operation_form: {
        operation_id: '',
        parent_id: '',
        permission_code: '',
        permission_remark: '',
        resource_type_id: 3,
        resource_type_code: "OPERATION",
        resource_type_name: "操作",
      },
      // 编辑--选项卡状态
      edit_tab_selected: 'edit_menu_form',
      // 编辑Dialog
      edit_dialog_visible: false,
      edit_menu_form: {
        menu_name: '',
        menu_order: '',
        route_id: null,
        route_name: '',
        route_full_path: '',
        parent_id: '',
        permission_remark: '',
        resource_type_id: 2,
        resource_type_code: "MENU",
        resource_type_name: "菜单",
      },
      edit_operation_form: {
        operation_id: '',
        parent_id: '',
        permission_code: '',
        permission_remark: '',
        resource_type_id: 3,
        resource_type_code: "OPERATION",
        resource_type_name: "操作",
      },
      rules: {
        menu_name: [{ required: true, type: 'string', message: '请输入菜单名称！', trigger: 'blur' }],
        route_id: [{ required: true, message: '请选择与之关联的路由！', trigger: 'change' }],
        menu_order: [
          { required: true, type: 'string', message: '请填写显示顺序！', trigger: 'blur' },
          { pattern: /^\d+$/, message: '顺序必须是数字组成！', trigger: 'blur' }
        ],
        operation_id: [{ required: true, message: '请选择操作按钮类型！', trigger: 'change' }],
        permission_code: [{ required: true, message: '请输入权限代码！', trigger: 'blur' }],
        permission_remark: [{ required: true, message: '请输入权限备注！', trigger: 'blur' }]
      },
      // 选择图标
      icon_modal_visible: false,
      icons: [],
      pageSize: 30,
      total_icon: 0,
      checked_icon: { icon_id: '', icon_name: '' },
    }
  },
  created() {
    document.title = "菜单管理";
    this.loadIcons(1);
    // 加载菜单列表
    this.load_menu_list({ type: 'flat' });
  },
  computed: {
    ...mapGetters('Route', ['routes_tree', 'route_data']),
    ...mapGetters('Menu', ['allMenus']),
    ...mapState('Operation', ['operations']),
  },
  watch: {
    // 监控操作按钮的id--添加行为
    'insert_operation_form.operation_id': {
      handler(operation_id) {
        // 父级的code代码
        let { permission_code: parent_code } = this.current_node;
        // 选中的操作按钮
        let operation = this.operations.find((operation) => operation.operation_id === operation_id);
        // 合成完整code代码
        this.insert_operation_form.permission_code = `${parent_code}:${operation?.operation_code}`;
      }
    },
    // 监控操作按钮的id--编辑行为
    'edit_operation_form.operation_id': {
      handler(operation_id) {
        let full_code;
        // 原始的operation_id、code代码
        let { operation_id: origin_operation_id, permission_code: origin_code } = this.current_node;
        // 如果是原始的状态，直接还原表单
        if (operation_id === origin_operation_id) {
          full_code = origin_code;
        }
        // 如果修改操作按钮类型
        if (operation_id !== origin_operation_id) {
          // 父级的code代码
          let arr = origin_code.split(':');
          // 去除 child_code
          arr.pop();
          // 计算出 parent_code
          let parent_code = arr.join(':');
          // 选中的操作按钮
          let operation = this.operations.find((operation) => operation.operation_id === operation_id);
          // 合成完整code代码
          full_code = `${parent_code}:${operation?.operation_code}`;
        }
        this.edit_operation_form.permission_code = full_code;
      }
    },
  },
  methods: {
    // 加载所有 action
    ...mapActions('Menu', ['load_menu_list', 'create_menu', 'edit_menu', 'remove_menu', 'set_menu_icon', 'create_menu_operation', 'edit_menu_operation', 'remove_menu_operation']),
    // 选择关联路由--change事件
    handleRouteChange(route_id, formName) {
      let { route_name, route_full_path } = this.route_data(route_id);
      this[formName].route_name = route_name;
      this[formName].route_full_path = route_full_path;
    },
    // 打开添加Dialog
    openInsertDialog(parent) {
      // 缓存父级权限id
      this.insert_menu_form.parent_id = parent.permission_id;
      this.insert_operation_form.parent_id = parent.permission_id;
      // 缓存父级权限code
      this.insert_menu_form.permission_code = parent.permission_code;
      this.insert_operation_form.permission_code = parent.permission_code;
      // 缓存当前操作行的指针
      this.current_node = parent;
      // 打开弹窗
      this.insert_dialog_visible = true;
    },
    // 根据选项卡，添加节点
    async handleInsert() {
      // 根据选项卡的值，确定是菜单 or 操作按钮
      let formName = this.insert_tab_selected;
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          // 添加菜单
          if (formName === 'insert_menu_form') {
            let { status, msg } = await this.create_menu({ ...this.insert_menu_form });
            if (status) {
              // 信息提示
              this.$message.success(msg);
              // 关闭Dialog
              this.insert_dialog_visible = false;
            } else {
              this.$message.error(msg);
            }
          }
          // 添加操作按钮
          if (formName === 'insert_operation_form') {
            let { status, msg } = await this.create_menu_operation({ ...this.insert_operation_form });
            if (status) {
              // 信息提示
              this.$message.success(msg);
              // 关闭Dialog
              this.insert_dialog_visible = false;
            } else {
              this.$message.error(msg);
            }
          }
        }
      })
    },
    // 打开编辑Dialog
    openEditDialog(nodeData) {
      // 根据父级类型，判断显示
      let formName = nodeData.resource_type_id === 2 ? 'edit_menu_form' : 'edit_operation_form';
      this.edit_tab_selected = formName;
      // 转存节点data数据
      this[formName] = { ...nodeData };
      // 缓存当前操作行的指针
      this.current_node = nodeData;
      // 打开弹窗
      this.edit_dialog_visible = true;
    },
    // 编辑节点
    async handleEdit() {
      // 对应的formName
      let formName = this.edit_tab_selected;
      // 表单验证
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          if (formName === 'edit_menu_form') {
            let { status, msg } = await this.edit_menu({ ...this.edit_menu_form })
            if (status) {
              // 更新节点
              Object.assign(this.current_node, this.edit_menu_form);
              // 信息提示
              this.$message.success(msg);
              // 关闭Dialog
              this.edit_dialog_visible = false;
            } else {
              this.$message.error(msg);
            }
          }
          if (formName === 'edit_operation_form') {
            let { status, msg } = await this.edit_menu_operation({ ...this.edit_operation_form })
            if (status) {
              // 更新节点
              Object.assign(this.current_node, this.edit_operation_form);
              // 信息提示
              this.$message.success(msg);
              // 关闭Dialog
              this.edit_dialog_visible = false;
            } else {
              this.$message.error(msg);
            }
          }
        }
      })
    },
    // 关闭dialog动画结束之后，清除之前的验证提示
    handleClosedDialog(action) {
      this.$refs[`${action}_menu_form`].resetFields();
      this.$refs[`${action}_operation_form`].resetFields();
    },
    // 删除节点
    handleRemove(node) {
      let { resource_type_id, resource_type_name } = node;
      this.$confirm(`此操作将永久删除该${resource_type_name}, 是否继续？`, { type: 'warning', })
        .then(async () => {
          // 删除菜单
          if (resource_type_id === 2) {
            let { status, msg } = await this.remove_menu({ ...node })
            if (status) {
              this.$message.success(msg);
            } else {
              this.$message.error(msg);
            }
          }
          // 删除操作按钮
          if (resource_type_id === 3) {
            let { status, msg } = await this.remove_menu_operation({ ...node })
            if (status) {
              this.$message.success(msg);
            } else {
              this.$message.error(msg);
            }
          }
        })
        .catch(() => {
          this.$message.info('已取消删除');
        });
    },
    // 打开图标选择Dialog
    openIconDialog(nodeData) {
      // 缓存当前操作行的指针
      this.current_node = nodeData;
      // 缓存当前图标信息
      this.checked_icon.icon_id = nodeData.icon_id;
      this.checked_icon.icon_name = nodeData.icon_name;
      // 打开弹窗
      this.icon_modal_visible = true;
    },
    // 加载element图标
    async loadIcons(pageIndex) {
      let { status, data, total } = await Icon.list({ pageSize: this.pageSize, pageIndex });
      if (status) {
        // 添加选择状态--布尔值
        data.forEach((item) => {
          item.checked = false
        });
        this.icons = data;
        this.total_icon = total;
      }
    },
    // 分页器改变页码数
    handlePageChange(pageIndex) {
      this.loadIcons(pageIndex);
    },
    // 选中图标
    handleCheckIcon(icon) {
      this.icons.forEach((item) => {
        // 切换选中状态
        item.checked = (item === icon) ? !icon.checked : false;
      });
      // 存储/取消选中的iconName
      this.checked_icon.icon_id = icon.checked ? icon.icon_id : '';
      this.checked_icon.icon_name = icon.checked ? icon.icon_name : '';
    },
    // 更新图标
    async handleUpdateIcon() {
      let { menu_id } = this.current_node;
      let { status, msg } = await this.set_menu_icon({ menu_id, ...this.checked_icon });
      if (status) {
        // 信息提示
        this.$message.success(msg);
        // 关闭Dialog
        this.icon_modal_visible = false;
      } else {
        // 信息提示
        this.$message.error(msg);
      }
    },
    // 关闭图标选择Dialog
    handleClosedIconDialog() {
      this.checked_icon = {
        icon_id: '',
        icon_name: '',
      };
    },
  }
}
</script>

<style lang="less" scoped>
ul.icon-list {
  overflow: hidden;
  list-style: none;
  padding: 0 !important;
  border: solid 1px #eaeefb;
  border-radius: 4px;
}

.icon-list li {
  float: left;
  width: 10%;
  text-align: center;
  height: 100px;
  line-height: 100px;
  color: #666;
  font-size: 10px;
  border-right: 1px solid #eee;
  border-bottom: 1px solid #eee;
  margin-right: -1px;
  margin-bottom: -1px;

  &::after {
    display: inline-block;
    content: "";
    height: 100%;
    vertical-align: middle;
  }

  span {
    display: inline-block;
    line-height: normal;
    vertical-align: middle;
    font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB",
      "Microsoft YaHei", SimSun, sans-serif;
    color: #99a9bf;
    transition: color 0.15s linear;
  }

  i {
    display: block;
    font-size: 26px;
    margin-bottom: 15px;
    color: #606266;
    transition: color 0.15s linear;
  }

  .icon-name {
    display: inline-block;
    padding: 0 3px;
    height: 1em;
  }

  &.checked,
  &.checked:hover {
    background-color: rgb(92, 182, 255);

    span,
    i {
      color: white;
    }
  }

  &:hover {

    span,
    i {
